.container-view-form {
    display: flex;
    justify-content: space-between;     
    font-family: 'Poppins', sans-serif;
    font-size: 0.8rem; 
    
}
.wrapper-list {
    width: 40%; 
    margin-top: 10rem;         
}
.wrapper-list h1 {
    text-align: center; 
    margin: 3rem;
        
}
.wrapper-list ul {
    width: 95%;  
    margin: 2px 1rem 0;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap; 
    height:auto; 
    box-sizing: border-box;   
}
.wrapper-list li {     
    width: 48%;  
    margin: 2px 2px 0;
    padding: 0.2rem;
    font-size: 0.8rem;
    background-color: white;
    border-radius: 2px;
    border: 1px solid black;
}
.separation {
    width: 0px;
    min-height: 80vh;
    margin:6rem 0 4rem 0; 
    border-right: 2px solid black;
}
.wrapper-form {
    width: 40%;
    margin-top: 10rem;     
}
.wrapper-form h1 {
   text-align: center;
   margin: 3rem;

}
.input-form {
    margin-left: 1rem;
    width: 15rem;
    height: 1rem;    
    padding: 0.2rem;
    border-radius: 5px;
    border: 2px solid #d8d8d8;
}
.input-form:focus {    
    outline: none !important;
    border:1px solid red;
    box-shadow: 0 0 10px #719ECE;
}
.btn-form {
    margin-top: 1rem;
    padding: 5px 15px;
    border-radius: 5px;
    border: 1px solid #cacaca;
    background-color: rgb(60, 161, 255);
    color: white;
    cursor: pointer;
}
.btn-form:hover {
    background-color: rgb(100, 178, 252);
}
